<template>
  <div class="container">
    <button class="icon" @click='() => this.open = !this.open'>
      <img :src='cog' style='height:20px; width: 20px;'/>
    </button>
    <div v-if='open' class='menu'>
      <div class="themeOptions">
        <p>Theme:</p>
      <select v-model="theme" class="select-css" @change="changeSelect">
        <option v-for='option in themeOptions' :value='option.value' :key='option.value'>
          {{option.display}}
        </option>
      </select>
      </div>
    </div>
  </div>
</template>

<script>
import Themes from '../themes.json'
export default {
  name: "Settings",
  data() {
    return{
      open: false,
    }
  },
  computed: {
    themeOptions() {
      return Object.keys(Themes).map((theme) => Themes[theme].displayName ? 
        {value:theme, display:Themes[theme].displayName} : 
        {value:theme, display:theme}
      )
    },
    theme() {
      return this.$store.state.theme
    },
    cog() {
      const theme = this.$store.state.theme;
      if(theme) {
        if(Themes[theme] && Themes[theme].icons && Themes[theme].icons.cog) {
          return Themes[theme].icons.cog;
        } else {
          return Themes.default.icons.cog
        }
      }
      return Themes.default.icons.cog
    },
  },
  methods: {
    changeSelect(e) {
      this.$store.commit('changeTheme', e.target.value);
      this.open = false;
    }
  }
};
</script>

